
body {
  overflow-x: hidden;
}

.main-image {
  background: url(https://images.unsplash.com/photo-1550399105-05c4a7641b02?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=2134&q=80);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  width: 100vw;
  height: 50vh;
  position: relative;
  background-attachment: fixed;
}

.filter:after {
  background-color: rgba(0, 0, 0, 0.5);
  content: '';
  display: block;
  height: 100%;
  left: 0;
  top: 0;
  position: absolute;
  width: 100%;
  z-index: 1;
}

.card-title {
  overflow: hidden;
  color: rgba(0, 0, 0, 0.85);
  font-weight: 500;
  font-size: 16px;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.load-more {

}

.details-flex-div{
  display: flex;
  justify-content: space-between;
  padding: 30px;
  margin: 10px;
  border-radius: 6px;
  box-shadow: 1px 1px 8px 0px #0000002b;

}

.bold-values{
  font-weight: 600;

}

.description-div{
  padding: 10px;
}

.button-control-div{
  display: flex;
  width: auto;
  align-items: center;
}

.default-btn{
  color: white;
  width: 75px;
  border-style: none;
  height: 40px;
  margin: 5px;
  border-radius: 5px;

}

.default-btn:focus{
  outline: none;
}

.buy-btn{
  background-color: #48a701;
}

.preview-btn{

  background-color: #e90026;
}

.info-btn{

  background-color: #001be5;
}

.link-style{
  text-decoration: none;
  list-style: none;
  color: white;
}

.link-style:visited{
  text-decoration: none;
  color: white;
}

.link-style:hover{
  text-decoration: none;
  color: white;
}

.link-style:active{
  text-decoration: none;
  color: white;
}

.link-style:link{
  text-decoration: none;
  color: white;
}

.card{
  height: 300px;
  transition: all 0.2s;
  cursor: pointer;
  box-shadow: 0px 0px 9px 0px #00000042;
  border-style: none;
  border-radius: 0px;
}

.card:hover{
  transform: scale(1.1);
}

.book-img{
  height: 100%;
}

.book-img:hover{

}

.title{
  padding: 15px;
}

.grid-container{
  padding: 50px;
  display: grid;
  gap: 2rem;
  row-gap: 2rem;
  grid-template-columns: repeat(auto-fill, minmax(150px, 1fr));
}

.loadmore-div{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

}

.brand-name{
  color: white;
  font-size: 4em;
  margin-bottom: 20px;
}
@media only screen and (max-width: 525px) {
  .details-flex-div{
    flex-direction: column;
    height: 550px;
  }

  .brand-name{
    font-size: 3em;
  }

}
